.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: max-content;
    padding-right: 12px;

    font-weight: 700;
    color: $color-white;
    opacity: 0;
    pointer-events: none;

    transition: opacity $transition-time;

    @extend %font-small-spaced;
  }

  @include onDesktop {
    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;
    }
  }

  &--menu {
    background-image: url(../images/menu.svg);

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
  }
}
